@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
:root {
  --basic-bg: #F3F7FA;
  --basic-white: #FFFFFF;
  --basic-blue: #2196F3;
  --main--font-color-dark: #4A4A4A;
  --main--font-color-light: #A0B0B9;
  --main--font-family: "Open Sans", "Arial", sans-serif;
  --aditional--font-family: "Gilroy", "Helvetica", sans-serif;
}
body {
  min-width: 754px;
  margin: 0 auto;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-family: var(--main--font-family);
  font-size: 12px;
  font-weight: 600;
  line-height: 18px;
  color: #4A4A4A;
  color: var(--main--font-color-dark);
  background-color: #F3F7FA;
  background-color: var(--basic-bg);
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  max-height: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}
.App {
  min-height: 100%;
  width: 754px;
  margin: 0 auto;
}
.outer-container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.inner-container {
  display: flex;
  flex-direction: column;
}
.logo {
    margin-top: 40px;
    margin-bottom: 32px;
}
.logo img {
    display: block;
    margin: 0 auto;
}
.transfer-filter {
    position: relative;
    padding: 20px 0 0 0;
    width: 232px;
    background-color: var(--basic-white);
    border-radius: 5px;
    box-shadow: 0 4px 4px 0 rgba(0,0,0,0.25);
}
.transfer-filter__title {
    text-transform: uppercase;    
    letter-spacing: 0.5px;
    font-size: 12px;
    line-height: 12px;
    margin: 0;
    padding: 0 20px;
}
.transfer-options {
    margin: 10px 0;
    padding: 0;
}
.transfer-options__option {
    padding: 11px 50px;
    list-style: none;
    font-size: 13px;
    font-weight: 400;
    position: relative;
}
.transfer-options__option span::first-letter {
    text-transform: capitalize;
}
.transfer-options__option:hover {
    background-color: #F1FCFF;
    cursor: pointer;
}
input[type="checkbox"] + span {
    display: block;
}
input[type="checkbox"] + span::before {
    position: absolute;
    top: 10px;
    left: 20px;
    width: 20px;
    height: 20px;
    content: "";    
    transition: all 0.5s ease-out;
}
input[type="checkbox"]  + span::before {    
    background-image: url(/BorisSidorenko/aviasales/static/media/checkbox-unchecked.ad129cbf.svg);    
}
input[type="checkbox"]:checked  + span::before {    
    background-image: url(/BorisSidorenko/aviasales/static/media/checkbox-checked.0fa36354.svg);    
}

.wrapper-loader {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, 0.5);
    z-index: 1;
}

.loader,
.loader:before,
.loader:after {
    border-radius: 50%;
    width: 1.5em;
    height: 1.5em;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation: load7 1.8s infinite ease-in-out;
    animation: load7 1.8s infinite ease-in-out;    
}
.loader {
    color: var(--basic-blue);
    font-size: 10px;
    margin: 0;
    position: absolute;
    text-indent: -9999em;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s;
    top: calc(50% - 15px);
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}
.loader:before,
.loader:after {
    content: '';
    position: absolute;
    top: 0;
}
.loader:before {
    left: -3.5em;
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s;
}
.loader:after {
    left: 3.5em;
}

@-webkit-keyframes load7 {
    0%,
    80%,
    100% {
        box-shadow: 0 1.5em 0 -1.3em;
    }
    40% {
        box-shadow: 0 1.5em 0 0;
    }
}

@keyframes load7 {
    0%,
    80%,
    100% {
        box-shadow: 0 1.5em 0 -1.3em;
    }
    40% {
        box-shadow: 0 1.5em 0 0;
    }
}

.ticket-type-sort {
    position: relative;
    display: flex;   
    margin-left: auto; 
    width: 504px;
}
.type-btn {        
    box-sizing: border-box;
    padding: 15px;
    width: 168px;
    font-family: inherit;
    font-size: 12px;
    letter-spacing: 0.5px;
    line-height: 20px;
    text-transform: uppercase;
    border: 0;
    box-shadow: inset 0 1px 0 #DFE5EC, inset 0 -1px 0 #DFE5EC, inset -1px 0 0 #DFE5EC;
    background-color: var(--basic-white);
    transition: all 0.5s ease-out;
}
.type-btn:first-child {
    border-radius: 5px 0 0 5px;
    box-shadow: inset 0 1px 0 #DFE5EC, inset 0 -1px 0 #DFE5EC, inset 1px 0 0 #DFE5EC, inset -1px 0 0 #DFE5EC;
}
.type-btn:last-child {
    border-radius: 0 5px 5px 0;
}
.type-btn.active {
    background-color: var(--basic-blue);
    color: var(--basic-white);
    box-shadow: 0 4px 4px 0 rgba(0,0,0,0.25);
}
.type-btn:hover {
    cursor: pointer;
}

.ticket {
    margin: 20px 0;    
    padding: 19px 20px 20px 22px;
    width: 502px;    
    background-color: var(--basic-white);
    box-sizing: border-box;
    border-radius: 5px;
    box-shadow: 0 4px 4px 0 rgba(0,0,0,0.10);
}
.ticket-header {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.price {
    margin: 0;
    padding: 0;
    font-size: 24px;
    line-height: 24px;
    color: var(--basic-blue);
}
.ticket-details {
    display: grid;   
    margin-bottom: 10px;
}
.ticket-details:last-child {
    margin-bottom: 0;
}
.ticket-details p {
    margin: 0;
    padding: 0;
}
.details-header,
.details-info {
    display: grid; 
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 20px;
}
.details-header p {
    letter-spacing: 0.5px;
    line-height: 18px;
    font-size: 12px;
    text-transform: uppercase;
    color: var(--main--font-color-light);
}
.details-info {    
    line-height: 21px;
    font-size: 14px;
    color: var(--main--font-color-dark);
}
.no-stops {
    margin-left: 20px;
}
.ticket-list {
    margin: 0;
    padding: 0;
}
.ticket-item {
    margin: 0;
    padding: 0;
    list-style-type: none;
}
.ticket-item-loading {
    position: relative;
    width: 502px;
    height: 184px;
}
.ticket-template {
    position: relative;
    margin: 20px 0;    
    padding: 19px 20px 20px 22px;
    width: 502px;    
    background-color: var(--basic-white);
    box-sizing: border-box;
    border-radius: 5px;
    box-shadow: 0 4px 4px 0 rgba(0,0,0,0.10);
}
.ticket-template-header {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.ticket-template-price {
    width: 147px;
    height: 24px;
}
.ticket-template-logo {
    width: 110px;
    height: 36px;
}
.ticket-template-header .ticket-template-price,
.ticket-template-header .ticket-template-logo,
.wrapper div {
    background-color: rgb(233, 231, 231);
}
.ticket-template-details {
    display: grid;   
    margin-bottom: 10px;
}
.ticket-template-details:last-child {
    margin-bottom: 0;
}
.ticket-template-details .ticket-template-header,
.ticket-template-details  .ticket-template-info {
    display: grid; 
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 20px;
    margin-bottom: 0;
}
.wrapper {
    display: flex;
    flex-direction: column;
}
.wrapper .route-header,
.wrapper .duration-header,
.wrapper .stops-header {
    width: 141px;
    height: 17px;
    margin-bottom: 5px;
}
.wrapper .route,
.wrapper .duration,
.wrapper .stops {
    width: 141px;
    height: 15px;
}
.ticket-template-row {
    margin-bottom: 15px;
}
.ticket-template-row:last-child {
    margin-bottom: 0;
}
.btn-more {
    padding: 15px 20px;
    margin-bottom: 50px;
    font-size: 12px;
    letter-spacing: 0.5px;
    line-height: 20px;
    font-weight: 600;
    text-transform: uppercase;
    font-family: var(--main--font-family);
    color: var(--basic-white);
    background-color: var(--basic-blue);
    border: 0;
    border-radius: 5px;
}
